body {  
  background-color: #23272b;
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  margin: 2rem auto;
  max-width: 600px;
  background-color: #fbfbfb;
  border-radius: 20px;
}

.suggestionSection {
  height: 400px;
}

#question {
  margin-top: 4rem;
  color: #33383e;
  font-family: 'Permanent Marker';
  font-size: 3rem;
}

.options {
  margin: 1rem 0 4rem .25rem
}


#activityCount {
  padding-left: .25rem;
}

h2, #activityList {
  color: black;
}

#first-row {
  padding: 5% 5% 0 5%;
}

#second-row {
  display: flex;
  justify-content: space-around;
}

#primary-btn {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
  text-align: center;
  vertical-align: middle;
  padding: .375rem .75rem;
  margin: 0 1rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
}

#primary-btn:hover {
  color:#007bff;
  background-color: #fff;
  cursor: pointer;
}

#success-btn {
  color: #fff;
  background-color: #28a745;
  border-color: #28a745;
  text-align: center;
  vertical-align: middle;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
}

#success-btn:hover {
  color:#28a745;
  background-color: #fff;
  cursor: pointer;
}

#danger-btn {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
  text-align: center;
  vertical-align: middle;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
}

#danger-btn:hover {
  color:#dc3545;
  background-color: #fff;
  cursor: pointer;
}

#fourth-row {
  display: flex;
  justify-content: center;
  padding-bottom: 2em;
}

@media only screen and (max-width: 600px) {
  .container {
    max-width: 350px;
  }

  h1 {
    margin-top: 2.5rem;
    font-size: 2rem;
  }
}
